<template>
  <div class="container max-w-4xl my-10">
    <div class="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4">
      <h1 class="text-xl text-center pb-3">{{ title }}</h1>
      <div class="flex flex-row h-px-100">
        <div class="w-4/5">
          <video ref="videoPlayer" class="video-js vjs-fluid"></video>
        </div>
        <div
          class="flex flex-no-wrap overflow-y-scroll flex-col w-1/5 shadow-md ml-3 border-gray-200 border-1"
        >
          <div
            v-for="(k, index) in keyEvents"
            class="moment"
            :key="index"
            v-on:click="skip(k.time)"
          >
            {{ k.name }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import videojs from "video.js";

export default {
  name: "VideoPlayer",
  props: {
    videoOptions: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      bcData: {},
      keyEvents: [],
      title: null,
      matchId: null,
      matchData: {},
      player: null,
      options: {
        autoplay: true,
        controls: true,
      },
      playerKey:
        "BCpkADawqM2QQRAICfpf23j4hDi7g-UlKZp6rNqo3jReJFEfkuhnjOfjJTUOryo27EoXjggPUBbgzBpPvNY7p-YM4Me1Sx3Khpn-FkMQqP5x66s_ULssIqU3V6C2s7odORMP7L_iAyuGwexk",
    };
  },
  async mounted() {
    let urlParams = new URLSearchParams(window.location.search);

    this.bcData = await this.getBC(urlParams.get("videoId"));
    this.title = this.bcData.name;
    this.matchId = this.bcData.custom_fields.matchid;

    this.getMatchData().then((res) => {
      this.matchData = res;
      this.extractMatchInfo();
    });

    this.options.sources = this.bcData.sources;
    this.player = videojs(
      this.$refs.videoPlayer,
      this.options,
      function onPlayerReady() {
        console.log("onPlayerReady", this);
      }
    );
    var track = new videojs.VideoTrack({
      src: "text.vtt",
      language: "en",
      kind: "commentary",
    });
    this.player.videoTracks().addTrack(track);

    // Get the current player's VideoTrackList object.
    var videoTrackList = this.player.videoTracks();

    // Listen to the "change" event.
    videoTrackList.addEventListener("change", function () {
      // Log the currently enabled VideoTrack label.
      for (var i = 0; i < videoTrackList.length; i++) {
        var track = videoTrackList[i];

        if (track.enabled) {
          videojs.log(track.label);
          return;
        }
      }
    });
  },
  beforeDestroy() {
    if (this.player) {
      this.player.dispose();
    }
  },
  methods: {
    skip(time) {
      this.player.currentTime(time * 60 - 60);
    },
    extractMatchInfo() {
      this.keyEvents.push({
        name: "Match Start",
        time: 0,
      });
      this.keyEvents.push({
        name: "Second Half",
        time: this.matchData.match_info.first_half_time + 3.35,
      });
      this.matchData.home_team.goals &&
        this.matchData.home_team.goals.forEach((goal) => {
          this.keyEvents.push({
            name: `${goal.type} - ${
              goal.period == "SecondHalf" ? "2nd" : "1st"
            } half - ${goal.min}`,
            time: goal.period == "SecondHalf" ? goal.min + 3.35 : goal.min,
          });
        });
      this.matchData.away_team.goals &&
        this.matchData.away_team.goals.forEach((goal) => {
          this.keyEvents.push({
            name: `${goal.type} - ${
              goal.period == "SecondHalf" ? "2nd" : "1st"
            } half - ${goal.min}`,
            time: goal.period == "SecondHalf" ? goal.min + 3.35 : goal.min,
          });
        });
      this.matchData.away_team.bookings &&
        this.matchData.away_team.bookings.forEach((goal) => {
          this.keyEvents.push({
            name: `${goal.type} - ${
              goal.period == "SecondHalf" ? "2nd" : "1st"
            } half - ${goal.min}`,
            time: goal.period == "SecondHalf" ? goal.min + 3.35 : goal.min,
          });
        });
      this.matchData.home_team.bookings &&
        this.matchData.home_team.bookings.forEach((goal) => {
          this.keyEvents.push({
            name: `${goal.type} - ${
              goal.period == "SecondHalf" ? "2nd" : "1st"
            } half - ${goal.min}`,
            time: goal.period == "SecondHalf" ? goal.min + 3.35 : goal.min,
          });
        });
      this.keyEvents.sort(function (a, b) {
        return a.time - b.time;
      });
    },
    getMatchData() {
      return new Promise((resolve, reject) => {
        var requestOptions = {
          method: "GET",
          redirect: "follow",
        };

        fetch(
          `https://gateway.ffa.football/football/m${this.matchId}/details`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
    getBC(videoId) {
      return new Promise((resolve, reject) => {
        var myHeaders = new Headers();
        myHeaders.append(
          "Accept",
          `application/json;pk=BCpkADawqM2QQRAICfpf23j4hDi7g-UlKZp6rNqo3jReJFEfkuhnjOfjJTUOryo27EoXjggPUBbgzBpPvNY7p-YM4Me1Sx3Khpn-FkMQqP5x66s_ULssIqU3V6C2s7odORMP7L_iAyuGwexk`
        );

        var requestOptions = {
          method: "GET",
          headers: myHeaders,
          redirect: "follow",
        };

        fetch(
          `https://edge.api.brightcove.com/playback/v1/accounts/6058022062001/videos/${videoId}`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
  },
};
</script>

<style scoped lang="postcss">
.video {
  /* override other styles to make responsive */
  width: inherit !important;
  height: inherit !important;
}
.moment {
  @apply border text-xs text-gray-700 bg-red-400 text-center;
}
</style>